import * as React from 'react';
import "./pageee.css";
import Grid from '@mui/material/Grid';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';

const Div = styled(Box)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : 'white',
  ...theme.typography.body2,
  padding: theme.spacing(3),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

const Pageee=()=>
{
  return (
   <section className='about'>
   <h1 className="myabout">
    A Little Bit About Me
   </h1>
    <Box sx={{ flexGrow: 1 }}>
      <Grid container spacing={1}>
        <Grid div xs={12}>
          <Div>
            <p className='p'>
            My name is Crodia Lepogo and I am a final year Software Development student at the
Belgium Campus ITversity, <br/> with an ever yearning need to grow, learn and upskill, mixed with
a soft spot for giving back and serving.
<br/> I am Currently pursuing a Diploma in Information Technology
             <br/> that has shaped me over the years into an "All Round Software Developer" with a taste for UI design :)
            </p>
            <br/>
            <br/>
            <h2> Nice To Meet You :)</h2>



          </Div>
        </Grid>
       
        
      </Grid>
    </Box>
    </section>
  );
}
export default Pageee
