import "./intro.css";
import Crodia from "../../img/Crodia.png"
import Grid from '@mui/material/Grid';
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Typical from 'react-typical';

const Div = styled(Box)(({ theme }) => ({
    backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#cf9FFF',
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  }));
  

const Intro=()=>
{
    return( 
        <div className="i">
        <Box sx={{ flexGrow: 1 }}>
        <Grid container spacing={2}>
        <Grid div xs={6}>
        <Div>  
        <div className="titleItem">
                       Hello :) <br/>I am Crodia Lepogo!
                    </div>   
                    <br/>
                    <div>
                    <Typical sx={{
                        
                    }}
        steps={['A Web Developer', 2000, 'A Mobile App Developer', 3000, 'A UI/UX Designer', 10000 ,'Im Really Just An All Round Great Software Developer :)']}
        loop={Infinity}
        wrapper="h1"
        
      />
                    </div>
                    

        </Div>
        </Grid>

        <Grid div xs={6}>
          <Div>
          
                
                
          <img src={Crodia} alt="" className="i-img"/>
          

    
       
         
          

          </Div>
          </Grid>
          
          
        </Grid>
        </Box>
        </div>
    );

}
export default Intro
